import React, { useState } from "react";
import "typeface-roboto";
import ReactToPrint from "react-to-print";
import "rc-input-number/assets/index.less";
import Button from '@material-ui/core/Button';
import TextField from "@material-ui/core/TextField";
import Checkbox from "@material-ui/core/Checkbox";
import "./App.css";

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}


function genreateRandomOperator() {
  return getRandomInt(0, 100) > 49 ? "+" : "-";
}

function App() {
  
  let pageRef = null;

  const [state, setState] = useState({
    count: 1,
    range: 10,
    randomSlot: false,
  });

  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.value || event.target.checked  });
  };

  function generatePages() {
    const pages = [];
  
    pages.push(generateOnePage(0));

    const correctedCount = parseInt(state.count) > 10? 10:parseInt(state.count)
    for (var i = 1; i < correctedCount; i++) {
      pages.push(generateOnePage(i));
    }
    return pages;
  }
  
  function generateOnePage(index) {
    return (
      <div className="page" key={index}>
        <div className="page-header">
          <div>
            Name: ___________
          </div>
          <div>Date:___________</div>
          <div>Score:___________</div>
        </div>
        <div className="content">
          {generateGroupWithin10(25)}
          {generateGroupWithin10(25)}
          {generateGroupWithin10(25)}
          {generateGroupWithin10(25)}
        </div>
        <div className="pagebreak"> </div>
      </div>
    );
  }

  function generateGroupWithin10(number) {
    const items = [];
    let correctedRange = parseInt(state.range);
    if (!Number.isInteger(correctedRange)) {
      correctedRange = 10;
    }
  
    for (var i = 0; i < number; i++) {
      const operator = genreateRandomOperator();
  
      let firstNumber =
        "+" === operator ? getRandomInt(0, correctedRange - 1) : getRandomInt(1, correctedRange);
  
      let secondNumber =
        "+" === operator
          ? getRandomInt(0, correctedRange - firstNumber)
          : getRandomInt(0, firstNumber);
  
      let result =
        "+" === operator
          ? firstNumber + secondNumber
          : firstNumber - secondNumber;
  
      const emptySlot = getRandomInt(1, 4);
  
      const emptyPlace = <span>(&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;)</span>;
      if (emptySlot === 1 && state.randomSlot) {
        firstNumber = emptyPlace;
      } else if (emptySlot === 2 && state.randomSlot) {
        secondNumber = emptyPlace;
      } else {
        result = "";
      }
  
      items.push(
        <div className="item" key={i}>
          {firstNumber} {operator} {secondNumber} = {result}
        </div>
      );
    }
  
    return <div className="group">{items}</div>;
  }
  
  return (
    <div>
      <div className="controllers">
        <div className="controllers">
          Number of Pages:
          <TextField
            id="count"
            name="count"
            type="number"
            defaultValue={state.count}
            onChange={handleChange}
            InputLabelProps={{
              shrink: true,
            }}
            inputProps={{ min: "1", max: "10", step: "1" }}
          />
        </div>
        <div className="controllers">
          Max Range:
          <TextField
            id="range"
            name="range"
            type="number"
            defaultValue={state.range}
            onChange={handleChange}
            InputLabelProps={{
              shrink: true,
            }}
            inputProps={{ min: "10", max: "100", step: "5" }}
          />
        </div>
        <div className="controllers">
          Random empty slot:
          <Checkbox
            checked={state.randomSlot}
            onChange={handleChange}
            name="randomSlot"
            color="primary"
          />
        </div>

        <ReactToPrint
          trigger={() => <Button variant="contained" color="primary">
          Print out
        </Button>}
          content={() => pageRef}
        />
      </div>
      <div
        className="pages"
        ref={(page) => {
          pageRef = page;
        }}
      >
        {generatePages(state.count, state.range)}
      </div>
    </div>
  );
}

export default App;

